<template>
  <div>
    <div class="demo-row" @click.stop="handleClick('alert')">
      <span class="demo-row-subtitle">消息提示</span>
      <span>当用户进行操作时会被触发，该对话框中断用户操作，直到用户确认知晓后才可关闭。</span>

      <div class="demo-row-content">
        <dog-button class="demo-message-btn">点击打开</dog-button>
      </div>
    </div>

    <div class="demo-row">
      <span class="demo-row-subtitle">确认消息</span>
      <span>提示用户确认其已经触发的动作，并询问是否进行此操作时会用到此对话框。</span>

      <div class="demo-row-content">
        <dog-button class="demo-message-btn" @click.stop="handleClick('confirm')">点击打开</dog-button>
      </div>
    </div>

    <div class="demo-row">
      <span class="demo-row-subtitle">自定义</span>
      <span>可自定义配置不同内容。</span>

      <div class="demo-row-content">
        <dog-button class="demo-message-btn" @click.stop="handleClick('custom-content')">点击打开</dog-button>
      </div>
    </div>

    <div class="demo-row">
      <span class="demo-row-subtitle">区分取消与关闭</span>
      <span>有些场景下，点击取消按钮与点击关闭按钮有着不同的含义。</span>

      <div class="demo-row-content">
        <dog-button class="demo-message-btn" @click.stop="handleClick('custom')">点击打开</dog-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    handleClick(selector) {
      const _self = this;
      switch (selector) {
        case "alert":
          this.$alert({
            title: "标题名称",
            content: "这是一段内容"
          });
          break;
        case "confirm":
          this.$confirm({
            title: "提示",
            content: "此操作不可撤回，确定么?"
          });
          break;
        case "custom-content":
          this.$confirm({
            title: "自定义提示",
            content: `<h1 style="color: red;">自定义HTML</h1>`
          });
          break;
        case "custom":
          this.$confirm({
            title: "自定义提示",
            content: "自定义内容",
            confirmButtonText: "保存",
            cancelButtonText: "放弃"
          });
          break;
      }
    }
  }
};
</script>
